*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  /* border: none; */
  box-sizing: border-box;
}

html,
body {
  height: 100%;
  min-width: 320px;
}
body{
  font-family: 'Raleway', sans-serif;
  color: rgb(22,22,22);
}
a{
  text-decoration: none;
}
li{
  list-style: none;
}
//colors:
$black: rgb(22,22,22);
$yellow: rgb(241,201,23);
$grey: rgb(176,176,176);
$white: rgb(255,255,255);

@mixin fontProp($fs, $fw, $ls, $c){
  #{font-size}: $fs;
  #{font-weight}: $fw;
  #{letter-spacing}: $ls;
  #{color}: $c;
}


/* //----------------header----------------- */
.header {
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 50;
  &__container {
    grid-area: header;
    margin: 0 auto;
    padding: 0 15px;
    min-height: 85px;
    background: rgb(22,22,22);
    display: grid;
    grid-auto-flow: column;
    align-items: center;
    justify-content: space-around;
    .logo__container {
      position: relative;
      width: 100px;
      height: 45px;
      display: grid;
      grid-template-rows: repeat(2, 1fr);
      grid-template-columns: repeat(3, 1fr);
      margin: 10px auto;

      img {
        position: relative;
        height: 35px;
        grid-column: 1/2;
        grid-row: 1/2;
        padding: 0;
      }

      h3 {
        grid-column: 2/4;
        grid-row: 1/2;
        align-self: end;
        padding: 0;
        @include fontProp(22px, 600, 1px, $white);
        span{
          font-size: 25px;
          padding: 0;
        }
      }


      h4 {
        grid-column: 1/4;
        grid-row: 2/3;
        text-align: justify;
        text-transform: uppercase;
        padding: 0;
        @include fontProp(13px, 500, 3px, $yellow);
      }
    }


    nav{
      display: grid;
      .navLinks{
        display: flex;
        flex-direction: row;

        a {
          text-decoration: none;
          padding: 0 15px;
          @include fontProp(15px, 400, 2px, $grey);
        }

        a.active {
          color: $yellow;
        }
      }
    }

  }

  a.active {
    color: $yellow;
  }

}

@media screen and (max-width: 768px) {
  .header{
    nav{
      .navLinks {
        a {
          padding: 0 8px;
          @include fontProp(12px, 400, 2px, $grey);
        }
      }
    }
  }


  .header{
    &__container{
      .logo__container {
        width: 60px;
        height: 27px;
        img{
          height: 18px;
        }
        h3{
          font-size: 14px;
          span{
            font-size: 18px;
          }
        }
        h4{
          font-size: 10px;
        }
      }
    }
  }
}

